import React from "react"
import "./Contact.css"

const Contact = () => {
 
  return (
    <>
    <div className="container" id="contact">
      <div className="row w-100 justify-content-center">
        <div className="col-md-12">
          <h4 className="text-white text-center">Contact With Me</h4>
        </div>
        <div className="col-md-7">
        <form action="https://formspree.io/f/meqdjwqa" method="POST"  class="emailform"  onsubmit="this.submit()">
          <label> Name : </label>
           <input type="text" className="from-contorl" name="name" required/>
          <label> Email : </label>
           <input type="email" className="from-contorl" name="email" required/>
          <label> Mobile : </label>
           <input type="text" className="from-contorl" name="mobile" required/>
           <textarea placeholder="Type Message Here" className="mt-3" name="msg" required></textarea>
           <button className="btn btn-success">Send Message</button>
          </form>

        </div>
        </div>
        <iframe src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d6265.769596331446!2d74.7262429307052!3d19.11138027387729!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x3bdcb081bce9f525%3A0x712cc57880990ca6!2sBalikashram%20Rd%2C%20Ahmednagar%2C%20Maharashtra!5e0!3m2!1sen!2sin!4v1662102511254!5m2!1sen!2sin"  className="w-100 mt-5" style={{'height':'400px'}}></iframe>
    </div>
    </>
  )
}

export default Contact
